<template>
  <div v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <div class="btn">
        <el-select placeholder="请选择年级" v-model="grade">
          <el-option
            v-for="item in gradeList"
            :label="item.label"
            :value="item.title"
            :key="item.id"
          ></el-option>
        </el-select>
      </div>
      <div class="btn">
        <el-select placeholder="请选择班级" v-model="title">
          <el-option
            v-for="item in classList"
            :label="item.label"
            :value="item.title"
            :key="item.id"
          ></el-option>
        </el-select>
        <el-button slot="append" icon="el-icon-search"></el-button>
      </div>
    </div>

    <el-table :data="tableData" border :header-cell-style="{background:'#f0f0f0'}" max-height="620">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="class_id" label="班级ID"></el-table-column>
      <!-- <el-table-column prop="id" label="年级"></el-table-column> -->
      <el-table-column prop="created_at" label="创建时间"></el-table-column>
      <el-table-column prop="notice_title" label="标题"></el-table-column>
      <el-table-column prop="notice_content" label="内容"></el-table-column>
    </el-table>
    <div class="block">
      <el-pagination
        @current-change="currentChange"
        :current-page.sync="current"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="sizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api/index.js";
export default {
  data() {
    return {
      loading: false,
      gradeList: [], // 班级列表
      classList: [], // 班级列表

      tableData: [],
      grade: "",
      title: "",
      current: 1,
      size: 10,
      total: 0,
    };
  },
  mounted() {
    // this.getAnnouncement();
    // this.getGrade();
    // this.getClass();
  },
  methods: {
    // 获取公告
    getAnnouncement() {
      var self = this;
      API.notice(self.current, self.size)
        .then((res) => {
          self.loading = false;
          // console.log(res);
          res.data.forEach((item) => {
            item.notice_title = item.notice_title.replace(/<[^>]+>/gi, "");
            item.notice_content = item.notice_content.replace(/<[^>]+>/gi, "");
          });
          self.tableData = res.data;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    currentChange(val) {
      var self = this;
      self.loading = true;
      self.current = val;
      API.notice(val, self.size)
        .then((res) => {
          self.loading = false;
          // console.log(res);
          res.data.forEach((item) => {
            item.notice_title = item.notice_title.replace(/<[^>]+>/gi, "");
            item.notice_content = item.notice_content.replace(/<[^>]+>/gi, "");
          });
          self.tableData = res.data;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    sizeChange(val) {
      var self = this;
      self.loading = true;
      self.size = val;
      API.notice(self.current, self.size)
        .then((res) => {
          self.loading = false;
          res.data.forEach((item) => {
            item.notice_title = item.notice_title.replace(/<[^>]+>/gi, "");
            item.notice_content = item.notice_content.replace(/<[^>]+>/gi, "");
          });
          self.tableData = res.data;
        })
        .catch((err) => {
          self.loading = false;
        });
    },

    // 获取年级
    getGrade() {
      var self = this;
      API.grades().then((res) => {
        self.gradeList = res.data;
      });
    },
    // 获取班级
    getClass() {
      var self = this;
      API.classes().then((res) => {
        self.classList = res.data;
      });
    },
  },
};
</script>

